<template lang="pug">
    v-container
        v-combobox(v-model="select_token"
                            :items="tokens"
                            label="筛选token")

        v-data-table(:headers="headers"
            :items="tabledata"
            :options.sync="options"
            :expanded.expanded="expanded"
            :single-expand="singleExpand"
            show-expand
            fixed-header
            item-key="hash"
            no-data-text="暂无数据"
            :server-items-length="total_rows"
            :footer-props="footer_props"
            class="elevation-4"
            )
            template(v-slot:top)
                v-toolbar(flat)
                    v-toolbar-title 交易记录
                    
                    

            template(v-slot:expanded-item="{ headers, item }")
                td(:colspan="headers.length")
                    v-card
                        v-card-text
                            v-row 
                                v-col(cols="4")
                                    h3 转出用户:
                                v-col(cols="4")
                                    h3 {{from_username}}
                            v-row 
                                v-col(cols="4")
                                    h3 转入用户:
                                v-col(cols="4")
                                    h3 {{to_username}}

                            v-row 
                                v-col(cols="4")
                                    h3 转出账户余额:
                                v-col(cols="4")
                                    h3(v-show="!need_transform") {{from_balance}}
                                    h3(v-show="need_transform") {{from_balance/1000000}}
                            v-row 
                                v-col(cols="4")
                                    h3 转入账户余额:
                                v-col(cols="4")
                                    h3(v-show="!need_transform") {{to_balance}}
                                    h3(v-show="need_transform") {{to_balance/1000000}}

                            v-row 
                                v-col(cols="4")
                                    h3 memo:
                                v-col(cols="4")
                                    h3 {{hash_memo}}
  
                        v-card-actions
                            v-row 
                                v-col(cols="2")
                                    v-btn(@click="show_memo(item)") 
                                        v-icon(style="height:auto") refresh
                                v-col(cols="3")
                                    v-btn(@click="need_transform=!need_transform") 余额变换
 
</template>

<script>
import * as api from "@/apis/wswebapi"
export default {
    data:()=>({
        need_transform:true,
        select_token:"",
        tokens:[{value:"1003019",text:"OKT"},{value:"1003018",text:"ODKT"}],
        height:400,
        footer_props:{'items-per-page-options':[10,20,50],'show-first-last-page':true},
        expanded: [],
        hash_memo:"测试memo",
        singleExpand:true,//只允许单行扩展
        // 分页数据管理
        options:{},
        headers:[{text:"发送人",value:"owner_address"},{text:"接收人",value:"to_address"},{text:"数量",value:"amount"},{text:"Token",value:"tokenAbbr"},{text:"哈希",value:"hash"}],
        tabledata:[],
        total_rows:10,
        from_balance:-1,
        to_balance:-1,
        from_username:"",
        to_username:"",
        admin_address:"TSUEgZw4vuyxFQK1MaCrSw1U2Liw3vhN7r",
        admin_username:"oldking",
    }),
    methods:{
        show_memo(item){
            api.GetMemoByHash({hash:item.hash}).then(res=>{
                if (res.code==200){
                    this.hash_memo=res.data;
                }else{
                    this.hash_memo=res.msg;
                }
            })
            this.update_address_balance(item);
            this.update_address_username(item)
        },
        // 更新双方余额
        update_address_balance(item){
            let token_type=item.type;
            let token_address=item.contract_address;
            let token_id =item.token_id;
            api.GetTrxTokenBalance(item.owner_address).then(res=>{
                if (token_type=="trc10"){
                    for ( i =0;i<res.tokenBalances.length;i++){
                        if (res.tokenBalances[i].name==token_id){
                            this.from_balance= res.tokenBalances[i].balance
                        }
                    }
                }else if (token_type=="trc20"){
                    for (var i =0;i<res.trc20token_balances.length;i++){
                        if (res.trc20token_balances[i].contract_address==token_address){
                            this.from_balance= res.trc20token_balances[i].balance
                        }
                    }
                }else{
                    for ( i =0;i<res.tokenBalances.length;i++){
                        if (res.tokenBalances[i].name=="_"){
                            this.from_balance= res.tokenBalances[i].balance
                        }
                    }
                }
          })
           api.GetTrxTokenBalance(item.to_address).then(res=>{
                if (token_type=="trc10"){
                    for ( i =0;i<res.tokenBalances.length;i++){
                        if (res.tokenBalances[i].name==token_id){
                            this.to_balance= res.tokenBalances[i].balance
                        }
                    }
                }else if (token_type=="trc20"){
                    for (var i =0;i<res.trc20token_balances.length;i++){
                        if (res.trc20token_balances[i].contract_address==token_address){
                            this.to_balance= res.trc20token_balances[i].balance
                        }
                    }
                }else{
                    for ( i =0;i<res.tokenBalances.length;i++){
                        if (res.tokenBalances[i].name=="_"){
                            this.to_balance= res.tokenBalances[i].balance
                        }
                    }
                }
          })
        },
        update_address_username(item){
            if (item.owner_address!=this.admin_address){
                api.GetUserNameByAddress({address:item.owner_address}).then(res=>{
                    if (res.code==200){
                        this.from_username= res.data
                    }else{
                        this.from_username= res.msg
                    }
                })
            }else{
                this.from_username= this.admin_username;
            }
            
            if (item.to_address!=this.admin_address){
                api.GetUserNameByAddress({address:item.to_address}).then(res=>{
                    if (res.code==200){
                        this.to_username= res.data
                    }else{
                        this.to_username= res.msg
                    }
                })
            }else{
                this.to_username= this.admin_username;
            }
            
        },
    },
    computed:{
    },
    watch:{
        options:{
            handler(){
                let params={address:this.$route.params.address,count:true,limit:this.options.itemsPerPage,start:this.options.itemsPerPage*(this.options.page-1)};
                if (this.select_token!=""){
                    params.tokens=this.select_token.value;
                }
                api.GetTrxTransferHistory(params).then(res=>{
                    this.tabledata=res.transfers;
                    this.total_rows=res.total;
                })
            },
            deep:true,
        },
        select_token(){
            let params={address:this.$route.params.address,count:true,limit:this.options.itemsPerPage,start:this.options.itemsPerPage*(this.options.page-1)};
            if (this.select_token!=""){
                params.tokens=this.select_token.value;
            }
            api.GetTrxTransferHistory(params).then(res=>{
                this.tabledata=res.transfers;
                this.total_rows=res.total;
            })
        }
    }
}
</script>