<template lang="pug">
    v-container
        v-combobox(v-model="select_token"
                            :items="tokens"
                            label="筛选token")
        v-data-table(:headers="headers"
            :items="tabledata"
            :options.sync="options"
            :expanded.expanded="expanded"
            :single-expand="singleExpand"
            show-expand
            fixed-header
            item-key="index"
            no-data-text="暂无数据"
            :server-items-length="total_rows"
            :footer-props="footer_props"
            class="elevation-4"
            )
            template(v-slot:expanded-item="{ headers, item }")
                td(:colspan="headers.length")
                    v-card
                        v-card-text
                            h3 username: {{username}}    
                        v-card-actions
                            v-btn(@click="show_username(item)") 查询用户名
                            v-btn(@click="go_transfer_history(item)") 跳转到交易记录
        

</template>

<script>
import * as api from "@/apis/wswebapi"
export default {
    data:()=>({
        username:"",
        select_token:"",
        tokens:[{value:"TSUEgZw4vuyxFQK1MaCrSw1U2Liw3vhN7r",text:"OKT"},{value:"TSfNVLZahyCqzgG2TDeUrA6udYbrBY7tTE",text:"ODKT"}],
        height:400,
        footer_props:{'items-per-page-options':[10,20,50],'show-first-last-page':true},
        expanded: [],
        singleExpand:true,//只允许单行扩展
        // 分页数据管理
        options:{},
        headers:[{text:"序号",value:"index"},{text:"地址",value:"address"},{text:"余额",value:"balance"}],
        tabledata:[],
        total_rows:10,
    }),
    methods:{
        show_username(item){
            api.GetUserNameByAddress({address:item.address}).then(res=>{
                if (res.code==200){
                    this.username= res.data
                }else{
                    this.username= res.msg
                }
            })
        },
        go_transfer_history(item){
            this.$router.push("/trx/tokentransfers/"+item.address);
        }
    },
    watch:{
        options:{
            handler(){
                let params={address:this.select_token.value,count:true,sort:"-balance",limit:this.options.itemsPerPage,start:this.options.itemsPerPage*(this.options.page-1)};
                api.GetTrxTokenHolders(params).then(res=>{
                    this.tabledata=res.data;
                    this.total_rows=res.total;
                })
            },
            deep:true,
        },
        select_token(){
            this.options.page=1;
            let params={address:this.select_token.value,count:true,sort:"-balance",limit:this.options.itemsPerPage,start:this.options.itemsPerPage*(this.options.page-1)};
            api.GetTrxTokenHolders(params).then(res=>{
                this.tabledata=res.data;
                this.total_rows=res.total;
            })
        }
    }
}
</script>