


<template lang="pug">
    v-container
        v-card 
            v-card-text
                p 
                  h3 用户名：{{username}}
                p 
                  h3 邮箱:{{email}}
                p 
                  h3 ID:{{trxaddress}}


        v-card
            v-card-text
                p 
                   h3 二维码名片
                v-img(:src="trxaddressqrcode" max-width="400" max-height="400")

        v-card(v-show="userauthgrade>0")
            v-card-text 
              p 
                h3 {{balance_text}}{{balance}}
            v-card-actions
              v-btn(v-show="userauthgrade>0" @click="go_token_transfers") 转账记录
              v-btn(v-show="userauthgrade>9" @click="go_token_holders") token持有者
              v-btn(v-show="userauthgrade>9" @click="go_transfer") 发起转账
</template>


<script>
import * as api from "@/apis/wswebapi"
import { get} from 'vuex-pathify'
  export default {
      data:()=>({
          text:"oldking",
          balance:-1,
          token_address:"TSUEgZw4vuyxFQK1MaCrSw1U2Liw3vhN7r",
      }),
      computed:{
        username:get('user/username'),
        email:get('user/email'),
        trxaddress:get('user/trxaddress'),
        trxaddressqrcode:get('user/trxaddressqrcode'),
        userauthgrade:get('user/userauthgrade'),
        isLogin:get('user/islogin'),
        balance_text(){
          if (this.balance<0){
            this.get_coin_balance();
          }
          return "余额："
        }
      },
      methods:{
        go_token_transfers(){
          this.$router.push("/trx/tokentransfers/"+this.trxaddress);
        },
        go_token_holders(){
          this.$router.push("/trx/tokenholders/");
        },
        go_transfer(){
          this.$router.push("/trx/transfer/");
        },
        get_coin_balance(){
          api.GetTrxTokenBalance(this.trxaddress).then(res=>{
            for (var i =0;i<res.trc20token_balances.length;i++){
                if (res.trc20token_balances[i].contract_address==this.token_address){
                    this.balance= res.trc20token_balances[i].balance
                }
            }
            for ( i =0;i<res.tokenBalances.length;i++){
                if (res.tokenBalances[i].owner_address==this.token_address){
                    this.balance= res.tokenBalances[i].balance
                }
            }
          })
        },
        go_login(){
          this.$router.push("/login");
        }

      },
     
     
      watch:{
        isLogin(){
            if (!this.isLogin){
                this.go_login()
            }
        },
        trxaddress(){
          api.GetTrxTokenBalance(this.trxaddress).then(res=>{

            for (var i =0;i<res.trc20token_balances.length;i++){
                if (res.trc20token_balances[i].contract_address==this.token_address){
                    this.balance= res.trc20token_balances[i].balance
                }
            }
            for ( i =0;i<res.tokenBalances.length;i++){
                if (res.tokenBalances[i].owner_address==this.token_address){
                    this.balance= res.tokenBalances[i].balance
                }
            }
          })
        }
      }

  }
</script>