// import * as webService from '@/apis/wsproxy';
const webService = require('./wsproxy');
// 常量
let WEB_ARI_BASE_URL="http://cdn.oldking.club:2086/v1/web";
let WEB_TIME_OUT=10000;
let WS_PROXY_URL="wss://cdn.oldking.club:2096/v1/wsproxy";
// 导出变量
let REQ_TOKEN="";
let PIC_CODE="";
let EMAIL_CODE="";
export{REQ_TOKEN,PIC_CODE,EMAIL_CODE};

webService.SetBaseUrl(WEB_ARI_BASE_URL);
webService.InitConnect(WS_PROXY_URL);
webService.SetTimeout(WEB_TIME_OUT);

// get 登陆状态查询
export const GetLoginStatus = () => { return webService.path_get(`/loginstatus`).then(res => res); };
// get 获取用户信息
export const GetUserInfo = () => { return webService.path_get(`/userinfo`).then(res => res); };
// get 图片验证码
export const GetPiccode = () => { return webService.path_get(`/piccode`).then(res => res); };
// post 邮箱验证码
export const GetEmailcode = params => { return webService.path_post(`/emailcode`,params).then(res => res); };
// post Web登陆
export const Login = params => { return webService.path_post(`/login`, params).then(res => res); };
// post Web注册
export const Register = params => { return webService.path_post(`/register`, params,{email:params["email"],emailcode:params["emailcode"]}).then(res => res); };
// 重置密码
export const ResetPassword = params => { return webService.path_post(`/resetpassword`, params,{email:params["email"],emailcode:params["emailcode"]}).then(res => res); };
// get Web登出
export const Logout = () => { return webService.path_get(`/logout`).then(res => res); };
// 获取trx地址
export const GetTrxAddress = () => { return webService.path_get(`/gettrxaddress`).then(res => res); };

// 获取用户有权限的数据库


// 获取数据库配置
export const GetCollectionConfig = (collection) => { return webService.path_get(`/db/getcollectionconfig`,{collection:collection}).then(res => res); };

// 获取分页数据
export const GetPageData=(collection,params)=>{return webService.path_get(`/db/${collection}/many`,params).then(res=>res)};



// tron浏览器接口
// 获取token持有者
export const GetTrxTokenHolders = (params) => { return webService.get(`https://apilist.tronscan.org/api/tokenholders`,params).then(res => res); };
// 获取转账记录
export const GetTrxTransferHistory = (params) => { return webService.get(`https://apilist.tronscan.org/api/trc10trc20-transfer`,params).then(res => res); };
// 通过hash获取memo
export const GetMemoByHash = (params) => { return webService.path_get(`/admin/trx/getMemoByHash`,params).then(res => res); };
// 通过地址获取用户名
export const GetUserNameByAddress = (params) => { return webService.path_get(`/admin/trx/getUsernameByAddress`,params).then(res => res); };
// 获取账户余额
export const GetTrxTokenBalance = (address) => { return webService.post(`https://apilist.tronscan.org/api/account`,{address:address,visible:true}).then(res =>res) };
// 用户互转trc10
export const TransferTrc10ByUsername = (params) => { return webService.path_post(`/admin/trx/tranferTrc10ByUsername`,params).then(res =>res) };
// 用户互转trc20
export const TransferTrc20ByUsername = (params) => { return webService.path_post(`/admin/trx/tranferTrc20ByUsername`,params).then(res =>res) };
// 用户互转trx
export const TransferTrxByUsername = (params) => { return webService.path_post(`/admin/trx/tranferTrxByUsername`,params).then(res =>res) };