<template lang="pug">
    v-container
        v-text-field(v-model="fromusername"
                label="转出用户"
                required)

        v-text-field(v-model="tousername"
                label="转入用户"
                required)

        v-combobox(v-model="select_token"
                            :items="tokens"
                            label="选择token")

        v-text-field(v-model="amount"
                label="数量"
                required)

        v-text-field(v-model="memo"
                label="备注(hexstring)"
                required)

        v-btn(@click="transfer") 发起转账
        
</template>

<script>
import { get} from 'vuex-pathify'
import * as api from "@/apis/wswebapi"
  export default {
      data:()=>({
          fromusername:"",
          tousername:"",
          amount:0,
          memo:"",
          select_token:{value:"1003019",text:"OKT"},
          tokens:[{value:"1003019",text:"OKT"},{value:"1003018",text:"ODKT"},{value:"TRX",text:"TRX"},{value:"TR7NHqjeKQxGTCi8q8ZY4pL8otSzgjLj6t",text:"USDT"}],
      }),
      computed:{
        trxaddress:get('user/trxaddress'),
      },
      methods:{
          transfer(){
              let params={fromusername:this.fromusername,tousername:this.tousername,Amount:parseInt(this.amount),tokenid:this.select_token.value,password:"lfmtpndc751",memo:this.memo};
              if (this.select_token.value=="TRX"){
                  api.TransferTrxByUsername(params).then(res=>{
                  console.log(res);
                  if (res.code==200){
                      this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"});
                    //   跳转到转账记录
                    this.$router.push('/trx/tokentransfers/'+this.trxaddress);
                  }else{
                      this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"});
                  }
              })
              }else if (this.select_token.text=="USDT"){
                  params.contract=this.select_token.value;
                  api.TransferTrc20ByUsername(params).then(res=>{
                  console.log(res);
                  if (res.code==200){
                      this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"});
                    //   跳转到转账记录
                    this.$router.push('/trx/tokentransfers/'+this.trxaddress);
                  }else{
                      this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"});
                  }
              })
              }else{
                  api.TransferTrc10ByUsername(params).then(res=>{
                  console.log(res);
                  if (res.code==200){
                      this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"});
                    //   跳转到转账记录
                    this.$router.push('/trx/tokentransfers/'+this.trxaddress);
                  }else{
                      this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"});
                  }
              })
              }
              
          }
      }
  }
</script>